@import url("https://webfontworld.github.io/goodchoice/Jalnan.css");
@import url("https://webfontworld.github.io/cookierun/CookieRun.css");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Noto+Sans+KR:wght@100..900&display=swap");

body {
  font-family: "Inter", sans-serif;
  background: url(/public/images/Rectangle\ 650.png) no-repeat;
  background-size: cover;
  height: 100vh;
}

.WeatherWrap {
  width: 60%;
  display: flex;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.514);
  border-right: none;
  backdrop-filter: blur(10px);
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.363);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.WeatherBox {
  width: 60%;
  text-align: center;
  background-color: rgba(0, 119, 255, 0.1);
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  border-radius: 15px 0 0 15px;
  color: #fff;
  padding: 2rem;
}

.WeatherBox h2 {
  font-weight: 700;
  font-size: 4rem;
  margin: 0;
}
.WeatherBox h3 {
  font-weight: 500;
  font-size: 1.5rem;
  margin: 10px;
}
.WeatherBox h4 {
  font-size: 1rem;
  font-weight: 400;
  margin: 0;
}

.WeatherButton {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  /* border-left: 1px solid rgba(255, 255, 255, 0.2); */
}
.WeatherButton button {
  font-family: "CookieRun";
  font-size: 1.2rem;
  border: none;
  background: none;
  color: #fff;
  border-radius: 10px;
  height: 2.5rem;
  width: 90%;
  cursor: pointer;
}

.WeatherButton button:hover {
  color: #000;
  border: 1px dotted #000;
}

.ClipLoader {
  position: absolute;
  top: 50%;
  left: 40%;
}
